@include mq(small){
    nav{
        display:flex;
        border-bottom:5px solid black;
        position:fixed;
        width:100vw;
        background-color: orange;
        z-index:1;
        }

nav ul{
    display:flex;
    width:100%;
    list-style-type:none;
    font-size:1.3rem;
    font-weight: bold;
    
   
}
nav ul a{
    width:50%;
    text-decoration: none;
   display:flex;
   justify-content: center;
   align-items: center;
}
/*Toggle*/
.navThirdPart{
    width:100%;
    display:flex;
    height:24px;
    
    justify-content: flex-end;
    }
    .toggle{
      width:30%;
        display:flex;
        align-items: center;
        border:2px solid yellow;
        border-radius:50px;
        background: gray;
       
        
    }
    .turner{
        width:40%;
        height:80%;
        border-radius:50px;
        border:2px solid black;
        background-color: red;
    }
    .on{
        transition:3s;
        transform:translateX(140%)
    
    }
    .off{
        transition:3s;
        transform:translateX(17%)
    }
    

}
